	module.exports = {"data":{"markdownRemark":{"html":"<p>「市場開発と権威基金：政治資源の保護と発展」という記事を続けて考えてみましょう。</p>\n<p>物質的資源に加えて、非物質的資源の重要性がますます顕著になっています。これは市場経済システムの発展のための論理です。非物質的資源の観点から、政治システムの重要な資源は、その制度的資源と規範的資源である。市場経済システムの段階的拡大の歴史的変化の過程で、中国の政治制度配分の資源構造は、物質的資源から非物質的資源へと徐々に移行しなければならない。成熟した市場経済システムの条件下では、非物質的資源は非常に重要な資源であり、重要な資源よりも重要な場合もあります。政治制度は、本質的に、非強力な資源を生み出す最も強力で能力がある。</p>\n<p>社会的近代化の過程では、非物質的資源の拡大も必要である。物質的資源は拡大しているが、非物質的資源の拡大なしには、協調的に発展することはできない。これは非常に重要な論理です。中国の社会経済と文化の急速な発展において、戦略的課題の1つは、制度的資源を拡大し、それに応じて政治システムの資源を調整することである。フレモント・カステルとジェームス・ロザンツウェイクは、「より敏感な個人と団結したグループは、組織の改善につながる可能性があります。そうした改善につながることもありません。関係、闘争は組織内のグループ間で発生する包括的な観点から、この結果は誤動作である可能性があります。 \"社会経済と活力の完全な立ち上げの間にこの個人とグループの利益の明快さ。重要なのは、社会バランスと社会運動を達成するために、それぞれの立場を決定するための一定の制度と厳格な基準を持つことである。</p>\n<p>政治的観点からは、目標の実現を促進するために必要な資源を集中させる目的で、社会経済開発のために必要な政治的支援資源を提供する。重要なリソースと非重要なリソースから始める必要があります。これはまた、社会開発における政治の役割を人々が再理解することを要求している。関係する主な問題は、政策選択、政治制度、政治秩序、政治心理などである。特定の社会が開発の目標を決定した後、それは全体的な目標の実現を促進するために可能な限り大きな政治的資源を開発すべきである。これに続く現代の諸国では、政治資源の蓄積と利用が近代化の成功の鍵である。したがって、政治制度の資源配分能力を理解するだけでなく、適切な戦略を立てることも必要である。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0196.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0196.md","frontmatter":{"title":"11月27日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0196.md absPath of file >>> MarkdownRemark"}}

